/* Typography */
/* Normalize  */
html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  overflow-x: hidden;
  font: inherit;
  padding: 0;
  margin: 0;
}

body {
  font: inherit;
  overflow: hidden;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  line-height: 1.556;
  font-weight: 400;
}

h1 {
  font-family: "Josefin Sans", sans-serif;
  font-size: 70px;
  line-height: 100px;
  font-weight: 700;
}

h2 {
  font-family: "Josefin Sans", sans-serif;
  font-size: 60px;
  line-height: 1.167;
}

h3 {
  font-family: "Josefin Sans", sans-serif;
  font-size: 36px;
  line-height: 46px;
  font-weight: 600;
}

h4 {
  font-family: "Josefin Sans", sans-serif;
  font-size: 24px;
  line-height: 34px;
  font-weight: 600;
}

p, a, li, span {
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  line-height: 1.556;
  font-weight: 400;
  padding: 0;
  margin: 0;
}

a {
  display: inline-block;
  font-family: "Open Sans", sans-serif;
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Josefin Sans", sans-serif;
  margin: 0;
  padding: 0;
}

p {
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
}

a {
  text-decoration: none;
  font-weight: 400;
}

a,
a:hover,
a:focus,
a:active {
  text-decoration: none;
  outline: none;
}

a i {
  padding: 0 2px;
}

img {
  max-width: 100%;
}

ul {
  padding: 0;
  margin: 0;
}
ul li {
  list-style: none;
}

/*input and button type focus outline disable*/
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=tel]:focus,
input[type=number]:focus,
textarea:focus,
input[type=button]:focus,
input[type=reset]:focus,
input[type=date]:focus,
input[type=submit]:focus,
select:focus,
button:focus {
  outline: none;
  box-shadow: none;
}

/* Global style */
.button-1 {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 34px;
  color: #ffffff;
  font-weight: 600;
  border-radius: 30px;
  width: 160px;
  height: 50px;
  text-transform: uppercase;
  background: linear-gradient(45deg, #903eff 0%, #3e19ff 100%);
  text-align: center;
}
.button-1:hover {
  color: #ffffff;
  background: linear-gradient(45deg, #3e19ff 0%, #903eff 100%);
}

.navbar-toggler {
  border: none !important;
  margin: 0px;
  padding: 0px;
}

.navbar-toggler:focus {
  outline: 0px;
}

h1.head {
  font-family: "Josefin Sans", sans-serif;
  font-size: 90px;
  line-height: 100px;
  font-weight: 700;
}

h2.title {
  font-family: "Josefin Sans", sans-serif;
  font-size: 60px;
  line-height: 70px;
}

h3.subtitle {
  font-family: "Josefin Sans", sans-serif;
  font-size: 36px;
  line-height: 46px;
  font-weight: 600;
}

h4.lasthead {
  font-family: "Josefin Sans", sans-serif;
  font-size: 24px;
  line-height: 34px;
  font-weight: 600;
}

p.text {
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  line-height: 28px;
  font-weight: 400;
}

/* Preloader Css */
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999999999;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #903eff 0%, #3e19ff 100%);
  overflow: hidden;
}
.preloader .preloader-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.preloader .preloader-inner .preloader-icon {
  width: 72px;
  height: 72px;
  display: inline-block;
  padding: 0px;
}
.preloader .preloader-inner .preloader-icon span {
  position: absolute;
  display: inline-block;
  width: 72px;
  height: 72px;
  border-radius: 100%;
  background: #fff;
  -webkit-animation: preloader-fx 1.6s linear infinite;
  animation: preloader-fx 1.6s linear infinite;
}
.preloader .preloader-inner .preloader-icon span:last-child {
  animation-delay: -0.8s;
  -webkit-animation-delay: -0.8s;
}

@keyframes preloader-fx {
  0% {
    -webkit-transform: scale(0, 0);
    transform: scale(0, 0);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    opacity: 0;
  }
}
@-webkit-keyframes preloader-fx {
  0% {
    -webkit-transform: scale(0, 0);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: scale(1, 1);
    opacity: 0;
  }
}
@-webkit-keyframes pulsani {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.002);
            transform: scale(1.002);
  }
  100% {
    -webkit-transform: scale(1.004);
            transform: scale(1.004);
    opacity: 0;
  }
}
@keyframes pulsani {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.002);
            transform: scale(1.002);
  }
  100% {
    -webkit-transform: scale(1.004);
            transform: scale(1.004);
    opacity: 0;
  }
}
@-webkit-keyframes trantani {
  0% {
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
  }
  50% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  100% {
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
  }
}
@keyframes trantani {
  0% {
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
  }
  50% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  100% {
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
  }
}
@-webkit-keyframes ura {
  0% {
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
    opacity: 1;
  }
  50% {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
    opacity: 0;
  }
}
@keyframes ura {
  0% {
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
    opacity: 1;
  }
  50% {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
    opacity: 0;
  }
}
