 /*******************************
   	All Responsive Code
   *******************************/

   @media (min-width: 2401px) {
	.banner {
		padding: 330px 0 240px;
	}

	.banner .bg-sape {
		left: 5%;
		width: 55%;
	}

	.platfromland {
		background-size: 100% 95%;
	}

	.process .maya {
		left: 1%;
	}

	.instruction-video .videoo .video-ilstation {
		left: 25%;
	}
}

@media (max-width: 2400px) {

	.banner {
		padding: 330px 0 240px;
	}

	.banner .bg-sape {
		left: 5%;
		width: 55%;
	}

	.platfromland {
		background-size: 100% 95%;
	}

	.process .maya {
		left: 1%;
	}

	.instruction-video .videoo .video-ilstation {
		left: 25%;
	}

}

@media (max-width: 1920px) {
	.banner {
		padding: 240px 0 150px;
	}

	.process .maya {
		left: -7%;
	}

}

@media (max-width: 1399px) {
	.banner {
		padding: 229px 0 150px;
	}


	h1.head {
		font-size: 70px;
		line-height: 80px;
	}

	.platfromland .lend .lend-box .item h3.lend-title,
	h2.title {
		font-size: 60px;
		line-height: 70px;
	}

	.process .maya {
		width: 25%;
	}

	.process .plane {
		top: -100px;
	}

	.lending .section-head {
		padding-top: 50px;
	}

}

@media (max-width: 1199px) {

	h1.head {
		font-size: 50px;
		line-height: 60px;
	}

	.platfromland .lend .lend-box .item h3.lend-title,
	h2.title {
		font-size: 42px;
		line-height: 52px;
	}

	h3.subtitle {
		font-size: 28px;
		line-height: 38px;
	}

	h4.lasthead {
		font-size: 22px;
		line-height: 32px;
	}


	#scrollUp {
		right: 0%;
	}

	.mein-menu .navbar #navbarNavDropdown ul.navbar-nav li.nav-item {
		margin-left: 20px;
	}

	.banner {
		padding: 229px 0 0px;
	}

	.banner .bg-sape {
		left: 5%;
		width: 45%;
	}

	.process .maya {
		display: none;
	}

	.process .plane {
		display: none;
	}

	.process .process-box {
		margin-top: 70px;
	}

	.process .section-head {
		margin-bottom: 0px;
	}

	.instruction-video .videoo .video-ilstation {
		left: 25%;
		width: 55%;
		top: 10%;
	}

	.footer .footer-box h4.lasthead {
		margin-top: 30px;
	}
}

@media (max-width: 991px) {

	.banner {
		padding: 188px 0 120px;
	}

	.banner .hero-area .banner-content p.text {
		font-size: 22px;
		line-height: 32px;
	}

	.mein-menu {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		background: -webkit-linear-gradient(45deg, rgba(144, 62, 255, 1) 0%, rgba(62, 25, 255, 1) 100%);
		background: linear-gradient(45deg, rgba(144, 62, 255, 1) 0%, rgba(62, 25, 255, 1) 100%);
		box-shadow: 3.801px 12.432px 24px 0px rgb(150 131 225 / 40%);
		z-index: 99999;
		padding: 10px 0 10px;
	}

	.mein-menu .navbar .navbar-toggler:focus {
		box-shadow: 0;
	}

	.mein-menu .navbar #navbarNavDropdown ul.navbar-nav {
		height: 350px;
		overflow: auto;
	}

	.mein-menu .navbar #navbarNavDropdown ul.navbar-nav {
		margin-left: auto;
		-webkit-box-align: start;
		-ms-flex-align: start;
		-ms-grid-row-align: flex-start;
		align-items: flex-start;
	}

	.mein-menu .navbar #navbarNavDropdown ul.navbar-nav li.nav-item {
		margin-left: 0;
		margin-top: 10px;
	}

	.mein-menu .navbar #navbarNavDropdown ul.navbar-nav li.nav-item a.nav-link {
		color: #fff;
	}

	.mein-menu .navbar #navbarNavDropdown ul.navbar-nav li.nav-item a.nav-link:before {
		top: calc(100% + 0px);
		background: #ffffff;
	}

	.mein-menu .navbar #navbarNavDropdown ul.navbar-nav li.nav-item .language-select .nice-select span {
		color: #fff;
	}

	.mein-menu .navbar #navbarNavDropdown ul.navbar-nav li.nav-item .language-select .nice-select:after {
		border-bottom: 2px solid #ffffff;
		border-right: 2px solid #ffffff;
		width: 10px;
		height: 10px;
	}

	.banner .hero-area .banner-content {
		padding-bottom: 30px;
	}

	.banner .hero-area .banner-content {
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
	}

	.banner .hero-area .right-box .nav {
		-webkit-box-pack: center;
		    -ms-flex-pack: center;
		        justify-content: center;
	}

	.banner .hero-area .right-box .nav li.nav-item:first-child {
		margin-right: 30px;
	}

	.banner .bg-sape {
		display: none;
	}

	.platfromland {
		background-size: cover;
		background-position: top center;
	}

	.platfromland .lend .lend-box {
		margin-top: 30px;
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
	}

	.platfromland .lend .lend-box .subscribe {
		padding: 30px 30px 30px;
	}

	.platfromland .lend .lend-box .item {
		padding: 30px 30px 30px;
		margin-top: 30px;
	}

	.platfromland .lend .lend-box .item:hover:before {
		height: 100%;
		border-radius: 10px;
	}

	.platfromland .lend .lend-box .item.active:before {
		height: 100%;
		border-radius: 10px;
	}

	.footer .footer-bottom .social-style {
		margin-bottom: 22px;
	}

	.Compare .Compare-box .responsive-table {
		overflow: auto;
	}

	.Compare .Compare-box .responsive-table .table {
		width: 992px;
	}

	.footer .footer-bottom .social-style a:first-child {
		margin-left: 0;
	}

	.lending {
		position: relative;
		padding: 120px 0 0px;
	}

	.footer .footer-bottom {
		-webkit-box-orient: vertical;
		-webkit-box-direction: reverse;
		    -ms-flex-direction: column-reverse;
		        flex-direction: column-reverse;
	}
}

@media (max-width: 767px) {

	.instruction-video .videoo .video-box {
		width: 70%;
	}

	.lending {
		background: -webkit-linear-gradient(top, #f0f1ff 0%, #ffffff 100%);
		background: linear-gradient(to bottom, #f0f1ff 0%, #ffffff 100%);
		padding: 116px 0 0px;
	}

	.lending .landing-bggg {
		display: none;
	}

	.lending .section-head {
		padding-top: 0px;
	}

}

@media (max-width: 575px) {
	.instruction-video .videoo .video-box {
		width: 95%;
	}

	.lending .section-head ul.list li.list-item {
		padding: 20px 10px 20px 20px;
	}

}

@media (max-width: 414px) {
	h1.head {
		font-size: 40px;
		line-height: 50px;
	}

	.platfromland .lend .lend-box .item h3.lend-title,
	h2.title {
		font-size: 35px;
		line-height: 45px;
	}

}

@media (max-width: 360px) {
	.banner .hero-area .right-box .nav {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		    -ms-flex-direction: column;
		        flex-direction: column;
	}

	.banner .hero-area .right-box .nav li.nav-item:first-child {
		margin-right: 0px;
		margin-bottom: 30px;
	}
}